<template>
  <img class="xi_guan" src="../../../../assets/images/xiguan.png" alt="" @click="nativePick">
</template>

<script>
import { hexToRgb } from "../../../../helpers";
export default {
  data() {
    return {
      hasEyeDrop: 'EyeDropper' in window
    };
  },
  props: {
      updateColor: Function,
    },
  methods: {
    async nativePick (e){
     const val =  e ? e.target.value : null
      if (val) {
        console.log('1: ' + val)
      } else {
        const eyeDropper = new window.EyeDropper() // EyeDropper
        console.log('Esc')
        try {
          const result = await eyeDropper.open() // 
          console.log('2: ' + result.sRGBHex)
          const color = hexToRgb(result.sRGBHex);
          if (color) {
              this.updateColor(color);
          }
        } catch (e) {
          console.log('')
        }
      }
    }
  },
};
</script>